.presentation-card {
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    background-color: var(--page-color);
    border-radius: var(--rounded-corners);
    min-height: 100vh;
}

.left-content {
    width: 40%; /* 40% del ancho total */
}

.right-content {
    width: 60%; /* 60% del ancho total */
    max-width: 45%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.right-content img {
    width: 100%;
    height: auto;
}

.name{
    font-size: 300%;
    color: var(--btn-color);
    text-align: start;
    margin-bottom: 0.5em;
    margin-top: 0.5em
}

.role{
    color: var(--text-color);
    text-align: start;
    font-size: 350%;
    margin-bottom: 0.5em;
    margin-top: 0.5em
}

.description{
    color: var(--text-color);
    font-size: 200%;
    text-align: start;
    margin-bottom: 0.5em;
    margin-top: 0.5em
}   

.btn{
    background-color: transparent;
    border-color: transparent;
    color: var(--btn-color);
    border-radius: var(--rounded-corners);
    font-size: 180%;
    padding: 1rem 1.2rem;
    gap: 0.5rem;
}

.btn:hover{
    text-decoration: none;
    color: var(--hover-text-color);
    background-color: var(--btn-color-hover);
}

.presentation-buttons-row {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
}

.presentation-buttons-row .btn {
  display: flex;
  align-items: center;
  justify-content: center;
}

.vertical-divider {
  width: 2px;
  height: 52px;
  background: linear-gradient(180deg, var(--hover-color) 0%, var(--card-color) 100%);
  align-self: center;
  border-radius: 2px;
  margin: 0 0.5rem;
}

/* Panda Illustration Styles */
.panda-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 350px;
    margin-top: 30px;
    position: relative; /* Para posicionar la figura amorfa */
}
.panda {
    position: relative;
    width: 200px;
    height: 260px;
}
.panda .ear {
    position: absolute;
    width: 60px;
    height: 60px;
    background: #222;
    border-radius: 50%;
    top: 0;
    z-index: 1;
}
.panda .ear.left {
    left: 0;
}
.panda .ear.right {
    right: 0;
}
.panda .head {
    position: absolute;
    top: 30px;
    left: 20px;
    width: 160px;
    height: 140px;
    background: #fff;
    border-radius: 50% 50% 45% 45%;
    z-index: 2;
    box-shadow: 0 2px 8px rgba(0,0,0,0.08);
}
.panda .eye {
    position: absolute;
    width: 38px;
    height: 48px;
    background: #222;
    border-radius: 50%;
    top: 60px;
}
.panda .eye.left {
    left: 25px;
    transform: rotate(-18deg);
}
.panda .eye.right {
    right: 25px;
    transform: rotate(18deg);
}
.panda .pupil {
    position: absolute;
    top: 18px;
    left: 12px;
    width: 14px;
    height: 14px;
    background: #fff;
    border-radius: 50%;
}
.panda .nose {
    position: absolute;
    top: 95px;
    left: 73px;
    width: 18px;
    height: 14px;
    background: #222;
    border-radius: 50% 50% 60% 60%;
}
.panda .mouth {
    position: absolute;
    top: 110px;
    left: 80px;
    width: 30px;
    height: 18px;
    border-bottom: 3px solid #222;
    border-radius: 0 0 30px 30px;
}
.panda .body {
    position: absolute;
    top: 140px;
    left: 30px;
    width: 140px;
    height: 110px;
    background: #fff;
    border-radius: 60% 60% 80% 80%;
    z-index: 1;
}
.panda .arm {
    position: absolute;
    width: 50px;
    height: 60px;
    background: #222;
    border-radius: 50%;
    top: 40px;
    z-index: 2;
}
.panda .arm.left {
    left: -20px;
    transform: rotate(-20deg);
}
.panda .arm.right {
    right: -20px;
    transform: rotate(20deg);
}
.panda .leg {
    position: absolute;
    width: 38px;
    height: 50px;
    background: #222;
    border-radius: 50%;
    bottom: -20px;
    z-index: 2;
}
.panda .leg.left {
    left: 10px;
    transform: rotate(-10deg);
}
.panda .leg.right {
    right: 10px;
    transform: rotate(10deg);
}
.panda .bamboo {
    position: absolute;
    left: 120px; /* Sube el bamb√∫ movi√©ndolo m√°s a la derecha */
    top: -10px;  /* Sube el bamb√∫ visualmente */
    z-index: 3;
    display: flex;
    flex-direction: column;
    align-items: center;
    transform: rotate(19deg); /* Inclina el bamb√∫ */
}
.panda .bamboo-stem {
    width: 16px;
    height: 110px;
    background: #6bbf59;
    border-radius: 8px;
    position: relative;
    box-shadow: 0 2px 0 #4e944f, 0 8px 0 #4e944f, 0 16px 0 #4e944f;
}
.panda .bamboo-leaf {
    position: absolute;
    width: 36px;
    height: 16px;
    background: linear-gradient(90deg, #4caf50 60%, #a3d977 100%);
    /* border: white 1px solid; */
    border-radius: 20px 60px 20px 60px / 20px 60px 20px 60px;
}
.panda .bamboo-leaf.leaf1 {
    left: 17px;
    top: 10px;
    transform: rotate(35deg);
}
.panda .bamboo-leaf.leaf2 {
    left: 17px;
    top: 40px;
    transform: rotate(10deg);
}
.panda .bamboo-leaf.leaf3 {
    left: 20px;
    top: 70px;
    transform: rotate(-10deg);
}
.panda-scale {
    /* Cambia el valor de scale para probar distintos tama√±os */
    transform: scale(2.4); /* Ejemplo: 1.2 = 120% del tama√±o original */
    transform-origin: center;
    transition: transform 0.2s;
    margin-top: -15vh; /* Sube el panda visualmente */
}

/* Figura amorfa detr√°s del panda */
.panda-bg-shape {
    position: absolute;
    scale: 3.7; /* Escala para que sea m√°s grande que el panda */
    rotate: 30deg; /* Rotaci√≥n para darle un efecto din√°mico */
    top: 50px;
    left: 40px; /* Ajusta la posici√≥n para que est√© detr√°s del panda */
    width: 200px;
    height: 130px;
    background: radial-gradient(ellipse at 60% 40%, var(--btn-color) 80%, var(--btn-color) 100%);
    border-radius: 10% 40% 10% 120% / 120% 120% 120% 90%;
    z-index: 0;
    opacity: 0.7;
    transform: rotate(-200deg) scale(1, 1.2);
}
